<template>
<main role="main" class="col-md-9  ml-sm-auto col-lg-10  px-4">
        <div class="container">
            <div class="row">
                <table class="table table-bordered mt-5  table-hover">
                    <thead class="thead-light">
                        <tr>
                            <th
                            v-for="(key, i) in columns"
                            v-bind:style="styleForColumns[key]"
                            :key="i">
                            {{ key }}
                            </th>
                            <th width="150">action</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr 
                        v-for="(item, index) in rows"
                        :key="index">
                            <td
                             v-for="(key, i) in columns"
                            :key="i">
                                <span v-if="editIndex !== index">{{ item[key] }}</span>
                                <span v-if="editIndex === index">
                                    <input class="form-control form-control-sm" v-bind:type="typeForInputs[key]" v-model="item[key]">
                                </span>
                            </td>
                            <td>
                                <span v-if="editIndex !== index">
                                    <button @click="edit(item, index)" class="btn btn-sm btn-outline-secondary mr-2 mb-1">Edit</button>
                                    <button @click="remove(item, index)" class="btn btn-sm btn-outline-secondary mr-2 mb-1">Remove</button>
                                </span>
                                <span v-else>
                                    <button class="btn btn-sm btn-outline-secondary mr-2" @click="cancel(item)">Cancel</button>
                                    <button class="btn btn-sm btn-outline-secondary mr-2" @click="save(item)">Save</button>
                                </span>
                            </td>
                        </tr>  
                    </tbody>
                </table>
                <div class="col-3 offset-9 text-right my-3">
                    <button v-show="!editIndex" @click="add" class="btn btn-sm btn-secondary">Add item</button>
                </div>
            </div>
        </div>
</main>
</template>

<script>
export default {
    name: 'table-content',
    props: {
        rows: Array,
        columns: Array,
        styleForColumns: Object,
        typeForInputs: Object
  },
    data() {
    return {
      editIndex: null,
      originalData: null
    }
  },
  methods: {
    add() {
      this.originalData = null
      this.rows.push({})
      this.editIndex = this.rows.length - 1
    },
    edit(item, index) {
      this.originalData = Object.assign({}, item)
      this.editIndex = index
    },
    cancel(item) {
      this.editIndex = null
      if (!this.originalData) {
        this.rows.splice(this.rows.indexOf(item), 1)
        return
      }
      Object.assign(item, this.originalData)
      this.originalData = null
    },
    remove(item, index) {
      this.rows.splice(index, 1)
    },
    save(item) {
      this.originalData = null
      this.editIndex = null
    }
  }
}
</script>

